  letter-spacing: 0.05em;
}
.form__btn:hover, .form__btn:focus {
  opacity: 0.5;
}
.form__btn:disabled {
  border-color: #74819c;
}
@media (max-width: 1199px) {
  .form__btn {
    max-width: none;
    margin-top: 30px;
  }
}
@media (max-width: 767px) {
  .form__btn {
    margin-top: 42px;
  }
}

.contact {
  padding: 56px 0 58px 120px;
  border-top: 1px solid #74819c;
}
@media (max-width: 1199px) {
  .contact {
    padding: 61px 0 57px 133px;
  }
}
@media (max-width: 767px) {
  .contact {
    padding: 64px 15px 57px;
  }
}

.contact__wrapper {
  display: grid;
  grid-template-columns: 170px max-content;
  row-gap: 16px;
  column-gap: 120px;
  position: relative;
  padding-left: 4px;
}
@media (max-width: 1199px) {
  .contact__wrapper {
    grid-template-columns: max-content max-content;
    column-gap: 96px;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .contact__wrapper {
    grid-template-columns: 1fr;
    gap: 18px;
  }
}
.contact__wrapper::after {
  content: "";
  display: block;
  height: 88%;
  width: 1px;
  background-color: #74819c;
  position: absolute;
  left: 235px;
  top: 3px;
}
@media (max-width: 1199px) {
  .contact__wrapper::after {
    left: 266px;
    top: 8px;
    height: 87px;
  }
}
@media (max-width: 767px) {
  .contact__wrapper::after {
    display: none;
  }
}

.contact__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  gap: 15px;
  max-width: 170px;
  list-style-type: none;
}
@media (max-width: 1199px) {
  .contact__list {
    max-width: 218px;
  }
}

.contact h2 {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  margin: 0;
  margin-bottom: 37px;
  letter-spacing: 0.03em;
  color: #102654;
}
@media (max-width: 1199px) {
  .contact h2 {
    font-size: 42px;
    line-height: 50px;
    margin-bottom: 32px;
  }
}
@media (max-width: 767px) {
  .contact h2 {
    font-size: 32px;
    line-height: 38px;
    margin-bottom: 34px;
  }
}

.contact__list h3 {
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  margin: 0;
  color: #102654;
}
@media (max-width: 1199px) {
  .contact__list h3 {
    line-height: 30px;
    font-size: 20px;
    margin-bottom: 5px;
  }
}

.contact__description {
  font-size: 14px;
  line-height: 21px;
  font-style: normal;
  margin: 0;
  padding-bottom: 5px;
  display: inline-block;
  word-break: break-all;
  color: #102654;
}
@media (max-width: 1199px) {
  .contact__description {
    font-size: 18px;
    line-height: 27px;
  }
}

.contact__link:hover, .contact__link:focus {
  opacity: 0.5;
}

.footer {
  background-color: #030a1a;
  padding: 42px 0;
}
@media (max-width: 1199px) {
  .footer {
    padding: 46px 0;
  }
}
@media (max-width: 767px) {
  .footer {
    padding: 46px 0;
  }
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .footer__wrapper {
    flex-direction: column;
    gap: 32px;
  }
}

.footer__logo {
  display: block;
  width: 88px;
  height: 32px;
  padding-top: 5px;
}
@media (max-width: 1199px) {
  .footer__logo {
    height: 18px;
    width: 82px;
    padding-top: 3px;
  }
}
.footer__logo svg {
  width: 100%;
}
@media (max-width: 1199px) {
  .footer__logo svg {
    height: 18px;
    width: 82px;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    position: absolute;
    margin: 0;
  }
}

.user-nav {
  max-width: 65%;
  margin-left: 11px;
}
@media (max-width: 1199px) {
  .user-nav {
    margin-left: 18px;
  }
}
@media (max-width: 767px) {
  .user-nav {
    margin-left: 0;
  }
}

.user-nav__list {
  display: grid;
  grid-template-columns: repeat(5, auto);
  gap: 15px 40px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1199px) {
  .user-nav__list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .user-nav__list {
    grid-template-columns: 130px 1fr;
    gap: 16px 30px;
  }
}

@media (max-width: 767px) {
  .user-nav__item--stock {
    grid-column: 1/-1;
    grid-row: 1/2;
    margin-left: 160px;
    order: -1;
  }
}

@media (max-width: 767px) {
  .user-nav__item--subscriptions {
    order: 3;
  }
}

@media (max-width: 767px) {
  .user-nav__item--coaches {
    order: 2;
  }
}

@media (max-width: 767px) {
  .user-nav__item--schedule {
    order: 4;
  }
}

.user-nav__list a {
  color: #ffffff;
  line-height: 16px;
  font-size: 14px;
}
@media (max-width: 767px) {
  .user-nav__list a {
    line-height: 19px;
    font-size: 16px;
  }
}
.user-nav__list a:hover, .user-nav__list a:active {
  opacity: 0.5;
}

.social {
  max-width: 100px;
  width: 100%;
  display: grid;
  grid-template-columns: max-content max-content max-content;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.social svg {
  fill: #ffffff;
}
@media (max-width: 767px) {
  .social {
    gap: 11px;
  }
}

.social__link:hover svg, .social__link:focus svg {
  fill: #d10a42;
}

